<template>
  <v-container fluid pa-r>
    <v-layout row wrap>
      <v-flex xs12>
        <h3 class="headline white--text secondary--text">의뢰요청</h3>
        <h4 class="caption white--text">Commitment to work or question</h4>
        <p class="primary--text">함께 성공적인 서비스를 만들어 가고자 하신다면 아래 양식에 따라 문의 해 주시기 바랍니다.</p>
      </v-flex>

      <v-flex xs12>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            v-model="name"
            :counter="10"
            :rules="nameRules"
            label="Name"
            required
            color="#FF0000"
            append-icon="close"
            prepend-icon="phone"
          ></v-text-field>
          <v-text-field
            v-model="email"
            :rules="emailRules"
            label="E-mail"
            required
            append-icon="close"
            prepend-icon="mail"
          ></v-text-field>

          <v-select
            v-model="select"
            :items="items"
            :rules="[v => !!v || 'Item is required']"
            label="문의 종류"
            required
          ></v-select>

          <v-textarea no-resize rows="9" name="comment" label="comment" value="의뢰하실 내용을 적어주세요" hint></v-textarea>

          <!--<v-btn :disabled="!valid" color="primary" @click="validate">Validate</v-btn>-->
          <v-btn color="accent" depressed @click="reset">보내기</v-btn>
        </v-form>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
export default {
  data: () => ({
    valid: true,
    name: '',
    nameRules: [
      v => !!v || 'Name is required',
      v => (v && v.length <= 10) || 'Name must be less than 10 characters'
    ],
    email: '',
    emailRules: [
      v => !!v || 'E-mail is required',
      v => /.+@.+/.test(v) || 'E-mail must be valid'
    ],
    select: null,
    items: ['시스템 개발', '시스템 컨설팅', '프로그램 제작', 'ATTO report'],
    checkbox: false
  }),

  methods: {
    validate () {
      if (this.$refs.form.validate()) {
        this.snackbar = true
      }
    },
    reset () {
      this.$refs.form.reset()
    }
    /* resetValidation() {
      this.$refs.form.resetValidation();
    } */
  }
}
</script>

<style lang="scss" scoped>
.v-icon {
  color: #fff !important;
}
</style>
